import React from 'react';
import App, { Container } from 'next/app';
import Loading from '../src/components/Loading';
// global.jQuery = global.$ = require('jquery'); 
// import "popper.js";
// import "bootstrap";

class DelayedWrapper extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      shouldRender: false
    };
  }

  componentDidMount() {
    if (this.props.delay) {
      setTimeout(
        () => this.setState({ shouldRender: true }),
        this.props.delay
      );
    } else {
      this.setState({ shouldRender: true });
    }
  }

  render() {
    return (
      <div style={this.state.shouldRender ? undefined : {display: 'none'}}>
        {this.props.children}
      </div>
    );
  }
}

class MyApp extends App {
  static async getInitialProps({ Component, ctx }) {
    let pageProps = {};

    if (Component.getInitialProps) {
      pageProps = await Component.getInitialProps(ctx);
    }

    return { pageProps };
  }

  constructor(props) {
    super(props);
    this.state = {loading: true};
  }

  componentDidMount() {
    this.setState({loading: false});
  }

  render() {
    const { Component, pageProps } = this.props;
    return (
      <Container>
        <Loading isLoading={this.state.loading} delay={500}>
        </Loading>
        <DelayedWrapper delay={200}>
          <Component {...pageProps} />
        </DelayedWrapper>
      </Container>
    );
  }
}

export default MyApp;